each(map-keys(@grid-breakpoints), #(@breakpoint) {
	#media-breakpoint-up(@breakpoint, {
		@infix: breakpoint-infix(@breakpoint, @grid-breakpoints);

		.list-group-horizontal@{infix} {
			flex-direction: row;

			.list-group-item {
				&:first-child {
					#border-bottom-left-radius(@list-group-border-radius);
					#border-top-right-radius(0);
				}

				&:last-child {
					#border-top-right-radius(@list-group-border-radius);
					#border-bottom-left-radius(0);
				}

				&.active {
					margin-top: 0;
				}

				& + .list-group-item {
					border-top-width: @list-group-border-width;
					border-left-width: 0;

					&.active {
						margin-left: -@list-group-border-width;
						border-left-width: @list-group-border-width;
					}
				}
			}
		}
	});
});


// Flush list items
//
// Remove borders and border-radius to keep list group items edge-to-edge. Most
// useful within other components (e.g., cards).

.list-group-flush {
	.list-group-item {
		border-right-width: 0;
		border-left-width: 0;
		#border-radius(0);

		&:first-child {
			border-top-width: 0;
		}
	}

	&:last-child {
		.list-group-item:last-child {
			border-bottom-width: 0;
		}
	}
}


// Contextual variants
//
// Add modifier classes to change text and background color on individual items.
// Organizationally, this must come after the `:hover` states.

each(@theme-colors, #(@value, @color) {
	#list-group-item-variant(@color, theme-color-level(@color, -9), theme-color-level(@color, 6));
});
